* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  list-style-type: none;
}

body {
  background: white(233, 225, 225);
}

h1 {
  color: #61dafb;
  margin-top: 20px;
  font-weight: 800;
}
.up {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;

  padding: 15px;
}

input {
  border-radius: 5px;
  color: #61dafb;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.05);
  border: 1px solid tomato;
  outline: none;
  margin-top: 25px;
  height: 2rem;
  font-size: 1.3rem;
  padding-left: 15px;
}

.meal {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  gap: 15px;
  width: 230px;
  color: #61dafb;
  padding: 5px;
}
.meal img {
  max-width: 130px;
  border-radius: 50%;
}

li {
  background: rgba(255, 99, 71, 0.911);
  border-radius: 20px;
  margin: 20px auto;
}
h2 {
  color: #61dafb;
  font-size: 2rem;
  font-weight: 900;
  color: black;
}

#country {
  font-size: 1.3rem;
  font-weight: 800;
}

a {
  outline: none;
  color: #61dafb;
  text-decoration: none;
  transition: 0.6s ease;
}
a:hover {
  color: black;
  text-decoration: none;
  scale: 1.2;
}

.box-xontainer {
  display: flex;
  flex-wrap: wrap;

  margin: 10px 0 5px;
}
